  enableSeek = true
  env = Env.env
  // ä¿å­å½åè¿è¡ç¯å¢çå¨å±æ¨¡å¼ï¼ä¸»è¦ç¨äºç§»å¨ç«¯-- ç«çå¨å±åæ¨ªæ¿å¨å±
  fullScreenMode: 'Vertical' | 'Horizontal' = 'Horizontal'
