import { Log } from 'mp4box'
import HTTPRequest from './HTTPRequest'
import { XHRLoader } from './XHRLoader'
import { RequestHeader } from '@/types/mp4'

class DownLoader {
  isActive: boolean = false
  realtime: boolean = false
  // chunkStartæçæ¯è¯·æ±çChunkå¨æ´ä¸ªæä»¶ä¸­çåå§åç§»é
  chunkStart: number = 0
  chunkSize: number = 0
  totalLength: number = 0
  chunkTimeout: number = 1000
  timeoutID: number | null = null
  url: string = ''
  callback: Function = null
  eof: boolean = false
  loader: XHRLoader

  constructor(url?: string) {
    this.url = url || ''
    this.loader = new XHRLoader()
  }

  // ä»å¼å¤´å»è¯·æ±æä»¶ï¼ä¹å°±æ¯åå§åæä»¶çè¯·æ±è¿ç¨ç´å°æææä»¶é½è¯·æ±å®æ
  start() {
    Log.info('Downloader', 'Starting file download')
    this.chunkStart = 0
    this.resume()
    return this
  }

  reset() {
    this.chunkStart = 0
    this.totalLength = 0
    this.eof = false
    return this
  }

  stop() {
    window.clearTimeout(this.timeoutID)
    this.timeoutID = null
    this.isActive = false
    return this
  }

  // resumeåstartä¸åçæ¯resumeå¯è½æ¯å¨æä»¶çè¯·æ±æååéæ°è®¾ç½®äºchunkStartä¹ååå»éæ°è¯·æ±æ°çchunk
  resume() {
    Log.info('Downloader', 'Resuming file download')
    this.isActive = true
    if (this.chunkSize === 0) {
      this.chunkSize = Infinity
    }
    this.getFile()
    return this
  }

  setUrl(_url: string): this {
    this.url = _url
    return this
  }

  setRealTime(_realtime: boolean): this {
    this.realtime = _realtime
    return this
  }

  setChunkSize(_size: number) {
    this.chunkSize = _size
    return this
  }

  setChunkStart(_start: number) {
    this.chunkStart = _start
    this.eof = false
    return this
  }

  setInterval(_timeout: number) {
    this.chunkTimeout = _timeout
    return this
  }

  setCallback(_callback: Function) {
    this.callback = _callback
    return this
  }

  getFileLength() {
    return this.totalLength
  }

  isStopped() {
    return !this.isActive
  }

  initHttpRequest(): HTTPRequest {
    let xhr = new XMLHttpRequest()
    let header: RequestHeader = {}
    ;(xhr as XMLHttpRequest & { [props: string]: any }).start = this.chunkStart
    if (this.chunkStart + this.chunkSize < Infinity) {
      let endRange = 0
      // chunkStart to (chunkStart + chunkSize - 1)
      let range = 'bytes=' + this.chunkStart + '-'
      endRange = this.chunkStart + this.chunkSize - 1
      range += endRange
      header.Range = range
    }
    let request = new HTTPRequest({
      url: this.url,
      header: header,
      method: 'get',
      xhr: xhr
    })

    return request
  }

  /**
   * @description åéç½ç»è¯·æ±ï¼è¯·æ±å¯¹åºçåªä½æä»¶
   * @returns
   */

  getFile() {
    if (this.isStopped()) return
    let ctx = this
    // eofä¸ºtrueè¡¨ç¤ºæ´ä¸ªåªä½æä»¶å·²ç»è¯·æ±å®æ¯
    if (ctx.totalLength !== 0 && ctx.chunkStart >= ctx.totalLength) {
      ctx.eof = true
    }
    if (ctx.eof === true) {
      Log.info('Downloader', 'File download done.')
      ctx.callback(null, true)
      return
    }
    let request = this.initHttpRequest()
    this.loader.load({
      request: request,
      error: error,
      success: success
    })

    function error(e) {
      ctx.callback(null, false, true)
    }

    function success(res) {
      // console.log(res)
      let xhr = this
      let rangeReceived = xhr.getResponseHeader('Content-Range')
      // console.log(rangeReceived) // bytes 0-9999999/28884979 // Refused to get unsafe header "content-range"
      if (ctx.totalLength === 0 && rangeReceived) {
        let sizeIndex
        sizeIndex = rangeReceived.indexOf('/')
        if (sizeIndex > -1) {
          ctx.totalLength = +rangeReceived.slice(sizeIndex + 1)
        }
      }
      // console.log(xhr)
      ctx.eof =
        xhr.response.byteLength !== ctx.chunkSize || xhr.response.byteLength === ctx.totalLength
      let buffer = xhr.response
      buffer.fileStart = xhr.start
      // console.log('æåæ¿å°è¯·æ±:', buffer)
      // {
      //   fileStart: 0,
      //   byteLength: 10000000,
      //   maxByteLength: 10000000,
      //   resizable: false
      // }
      // æ¿å°æ°æ®ä¹åæ§è¡åè°å½æ°
      ctx.callback(buffer, ctx.eof)
      // å¦æä¸è½½å¨è¿æ¯å¤äºæ¿æ´»ç¶æä¸è¿æ²¡å¨é¨ä¸è½½å®æçè¯
      if (ctx.isActive === true && ctx.eof === false) {
        let timeoutDuration = ctx.chunkTimeout
        ctx.timeoutID = window.setTimeout(ctx.getFile.bind(ctx), timeoutDuration)
      }
    }
  }
}

export { DownLoader }
