/**
 * @description æ¥çå½åçé¼ æ ä½ç½®æ¯å¦å¨ç¶åç´ åç»å¯¹å®ä½çå­åç´ çç»åèå´åï¼å¦æè¶åºåè¿åfalse
 * @param parent
 * @param topChild
 * @param pageX
 * @param pageY
 * @returns {boolean}
 */
export function checkIsMouseInRange(
  parent: HTMLElement,
  topChild: HTMLElement,
  bottom: number,
  pageX: number,
  pageY: number
) {
  let { x, y } = getDOMPoint(parent)
  let allTop = y - bottom - topChild.clientHeight
  let allBottom = y + parent.clientHeight
  let allLeft = x + Math.round(parent.clientWidth / 2) - Math.round(topChild.clientWidth / 2)
  let allRight = x + Math.round(parent.clientWidth / 2) + Math.round(topChild.clientWidth / 2)
  let parentLeft = x
  let parentRight = x + parent.clientWidth
  if (pageX >= allLeft && pageX <= allRight && pageY <= y && pageY >= allTop) return true
  if (
    pageX >= parentLeft - 5 &&
    pageX <= parentRight + 5 &&
    pageY >= y - 5 &&
    pageY <= allBottom + 5
  )
    return true
  return false
}

const SELECTOR_REG = /([\w-]+)?(?:#([\w-]+))?(?:\.([\w-]+))?/

/**
 * @description æ ¹æ®descçæ ç­¾æè¿°åpropsçå±æ§æè¿°æ¥åå»ºä¸ä¸ªDOMå¯¹è±¡ï¼å¹¶ä¸å¨å®ä¾ä¸æè½½åç§å±æ§
 * @param {string} desc
 * @param {DOMProps} props
 * @param {Node[]} children
 * @returns
 */
export function $<T extends ExternalHTMLElement>(
  desc?: string,
  props?: DOMProps,
  children?: string | Array<Node>
): T {
  let match: Array<string | undefined> = []
  let regArray = SELECTOR_REG.exec(desc)
  // /([\w-]+)?(?:#([\w-]+))?(?:\.([\w-]+))?/.exec('div#app.a.b')
  // Â ['div#app.a', 'div', 'app', 'a', index: 0, input: 'div#app.a.b', groups: undefined]
  match[0] = regArray ? regArray[1] : undefined // div
  match[1] = regArray ? regArray[2] : undefined // app
  match[2] = regArray ? regArray[3] : undefined // a
  let el = match[0] ? document.createElement(match[0]) : document.createElement('div')

  if (match[1]) {
    el.id = match[1]
  }
  match[2] && addClass(el, [match[2]])
  for (let key in props) {
    if (typeof props[key] === 'object') {
      if (key === 'style') {
        let str = ''
        let styles = props[key]
        for (let k in styles) {
          str += `${k}: ${styles[k]};`
        }
        el.setAttribute('style', str)
      } else {
      }
    } else {
      el.setAttribute(key, String(props[key]))
    }
  }
  if (typeof children === 'string') {
    el.innerHTML += children
  } else if (children) {
    for (let child of children) {
      el.appendChild(child.el)
    }
  }

  return el as T
}
/**
 * @description æ ¹æ®ä¼ å¥çå­ç¬¦ä¸²è·åå¯¹åºçDOMåç´ 
 * @param dom
 * @returns {HTMLElement | null}
 */
export function getEl(dom: HTMLElement | string): HTMLElement | null {
  if (dom instanceof HTMLElement) return dom
  if (typeof dom === 'string') {
    return document.querySelector(dom)
  }
  return null
}

export function addClass(dom: Element, classNames: Array<string>) {
  let classList = dom.classList
  for (let name of classNames) {
    if (!includeClass(dom, name)) {
      classList.add(name)
    }
  }
}

export function removeClass(dom: Element, classNames: Array<string>) {
  let classList = dom.classList
  classList.remove(...classNames)
}

export function changeClass(dom: Element, className) {
  dom.className = className
}

export function includeClass(dom: Element, className: string): boolean {
  let classList = dom.classList
  for (let key in classList) {
    if (classList[key] === className) return true
  }
  return false
}

