import { DanmakuData, Track } from '@/types/danmaku'
import { Player } from '@/page/player'
import { PriorityQueue } from './utils/PriorityQueue'
import { $ } from '@/utils/domUtils'

/**
 * @description å¼¹å¹ç±»ï¼åªä¸æ³¨äºå®ç°å¼¹å¹çåºæ¬é»è¾ï¼Viewå±
 */

let flag = false

export class Danmaku {
  private queue: PriorityQueue<DanmakuData>
  private moovingQueue: DanmakuData[] = [] // æ­£å¨è¿å¨ä¸­çå¼¹å¹
  private container: HTMLElement
  private player: Player
  private timer: number | null = null
  private renderInterval: number = 100
  // æ¯ä¸æ¡å¼¹å¹è½¨éçé«åº¦é»è®¤ä¸º10px
  private trackHeight: number = 10
  // æ»å±çè½¨éæ°ç®
  private trackNumber: number
  private opacity: number = 1
  private fontSizeScale: number = 1
  private isHidden = false

  private isPaused = true

  // å¼¹å¹å æ®å±å¹çå°ºå¯¸ï¼é»è®¤å æ®ä¸åå±å¹
  private showScale = 1 / 2

  private tracks: Array<{
    track: Track
    datas: DanmakuData[]
  }>
  private defaultDanma: DanmakuData = {
    message: 'default message',
    fontColor: '#fff',
    fontSize: this.trackHeight,
    fontFamily: '',
    fontWeight: 500
  }

  constructor(container: HTMLElement, player: Player) {
    this.queue = new PriorityQueue<DanmakuData>()
    this.container = container // div.video-danmaku-container
    this.player = player
    // é»è®¤çè½¨éæ°ç®å æ®å±å¹çä¸å
    this.trackNumber = this.container.clientHeight / 2 / this.trackHeight
    this.tracks = new Array(this.container.clientHeight / this.trackHeight)
    this.init()
  }

  init() {
    for (let i = 0; i < this.tracks.length; i++) {
      if (!this.tracks[i]) {
        this.tracks[i] = {
          track: {
            id: 0,
            priority: 0
          },
          datas: []
        }
      }
      this.tracks[i].track = {
        id: i,
        priority: 15 - i // è½¨éçä¼åçº§
      }
    }
  }

  // æåææçå¼¹å¹
  pause() {
    this.setPaused(true)
    this.moovingQueue.forEach((data) => {
      this.pauseOneData(data)
    })
  }

  // æ¢å¤å¼¹å¹çè¿å¨,æ¢å¤å¼¹å¹è¿å¨æ­¤å¤çé»è¾æé®é¢(å·²ä¿®å¤)
  resume() {
    this.setPaused(false)
    this.timer = window.setTimeout(() => {
      this.render()
    }, this.renderInterval)
    this.moovingQueue.forEach((data) => {
      this.resumeOneData(data)
    })
  }

  // æ¢å¤åæ¡å¼¹å¹çè¿å¨
  resumeOneData(data) {
    data.dom.style.transform = `translateX(-${data.totalDistance}px)`
    data.startTime = Date.now()
    data.rollTime = (data.totalDistance - data.rollDistance) / data.rollSpeed
    data.dom.style.transition = `transform ${data.rollTime}s linear`
  }

  // æååæ¡å¼¹å¹çè¿å¨
  pauseOneData(data) {
    let currentRollDistance = ((Date.now() - data.startTime) * data.rollSpeed) / 1000
    data.rollDistance = currentRollDistance + (data.rollDistance ? data.rollDistance : 0)
    data.dom.style.transition = ''
    data.dom.style.transform = `translateX(${-data.rollDistance}px)`
  }

  startDanmaku() {
    this.render()
  }

  // åç¼å²åºæ·»å æ­£ç¡®æ ¼å¼çå¼¹å¹
  addData(data: any) {
    this.queue.push(this.parseData(data))

    // å¦ææ£æµå°ç¼å²åºå¼¹å¹ä¸º0,ä¹å°±æ¯å®æ¶å¨è¢«å³é­çè¯å°±éæ°å¼å¯å®æ¶å¨
    // if (flag) return
    if (this.timer === null) {
      this.render()
      // nextTick(() => {
      //   this.render()
      // })
      // flag = true
    }
  }

  parseData(data: any): DanmakuData {
    if (typeof data === 'string') {
      return {
        message: data,
        fontColor: '#fff',
        fontSize: this.trackHeight,
        fontFamily: '',
        fontWeight: 500,
        timestamp: this.player.video.currentTime
      }
    }
    if (typeof data === 'object') {
      if (!data.message || data.message === '') {
        throw new Error(`ä¼ å¥çå¼¹å¹æ°æ®${data}ä¸åæ³`)
      }
      return Object.assign({ ...this.defaultDanma, timestamp: this.player.video.currentTime }, data)
    }
    throw new Error(`ä¼ å¥çå¼¹å¹æ°æ®${data}ä¸åæ³`)
  }

  render() {
    try {
      this.renderToDOM()
    } finally {
      this.renderEnd()
    }
  }

  renderEnd() {
    if (this.queue.length === 0) {
      window.clearTimeout(this.timer)
      this.timer = null
      flag = false
    } else {
      this.timer = window.setTimeout(() => {
        this.render()
      }, this.renderInterval)
    }
  }

  // åæå®çDOMåç´ ä¸æ¸²æä¸æ¡å¼¹å¹
  renderToDOM() {
    if (this.queue.length === 0) return
    let data = this.queue.shift()
    if (!data.dom) {
      let dom = $('div.video-danmaku-message')
      dom.innerText = data.message
      if (data.fontFamily !== '') {
        dom.style.fontFamily = data.fontFamily
      }
      dom.style.color = data.fontColor
      dom.style.fontSize = data.fontSize * this.fontSizeScale + 'px'
      dom.style.fontWeight = data.fontWeight + ''
      dom.style.position = 'absolute'
      dom.style.left = '100%'
      dom.style.whiteSpace = 'nowrap'
      dom.style.willChange = 'transform'
      dom.style.cursor = 'pointer'
      dom.style.opacity = this.opacity + ''
      dom.style.visibility = this.isHidden ? 'hidden' : ''
      data.dom = dom
    }
    data.dom.style.fontSize = data.fontSize * this.fontSizeScale + 'px'
    data.dom.style.opacity = this.opacity + ''
    this.container.appendChild(data.dom)
    data.totalDistance = this.container.clientWidth + data.dom.clientWidth
    data.width = data.dom.clientWidth
    // å¼¹å¹çæ»çä½ç§»æ¶é´ï¼ç¨äºè®¡ç®å¼¹å¹çéåº¦
    data.rollTime =
      data.rollTime || Math.floor(data.totalDistance * 0.0058 * (Math.random() * 0.3 + 0.7))
    // å¼¹å¹çç§»å¨éåº¦
    data.rollSpeed = parseFloat((data.totalDistance / data.rollTime).toFixed(2))
    // useTracksæè¿°çæ¯è¯¥å¼¹å¹å ç¨äºå¤å°ä¸ªè½¨é
    data.useTracks = Math.ceil(data.dom.clientHeight / this.trackHeight)
    // éç¹ï¼æ­¤å¤æ°ç»yçä½ç¨æ¯è¡¨æè¯¥å¼¹å¹å çè½¨éçidæ°ç»
    data.y = []

    this.addDataToTrack(data)

    if (data.y.length === 0) {
      if ([...this.container.childNodes].includes(data.dom)) {
        this.container.removeChild(data.dom)
      }
      this.queue.push(data)
    } else {
      data.dom.style.top = data.y[0] * this.trackHeight + 'px'
      this.startAnimate(data) // å¼å¯å¼¹å¹çå¨ç»
    }

    data.dom.ontransitionstart = (e) => {
      data.startTime = Date.now()
    }

    // data.dom.onmouseenter = () => {
    //   if (this.isStopped) return
    //   this.pauseOneData(data)
    // }

    // data.dom.onmouseleave = () => {
    //   if (this.isStopped) return
    //   this.resumeOneData(data)
    // }
  }

  // å°æå®çdataæ·»å å°å¼¹å¹è½¨éä¸
  addDataToTrack(data: DanmakuData) {
    // console.log(this.tracks)
    // [
    //   {track: {id:0, priority: 15}, datas: DanmakuData[]},
    //   {track: {id:1, priority: 14}, datas: DanmakuData[]},
    //   {track: {id:2, priority: 13}, datas: DanmakuData[]},
    //   {track: {id:14, priority: 1}, datas: DanmakuData[]},
    // ]
    // console.log(this.trackNumber)
    let y = []
    for (let i = 0; i < this.trackNumber; i++) {
      let track = this.tracks[i]
      let datas = track.datas

      if (datas.length === 0) {
        y.push(i)
      } else {
        let lastItem = datas[datas.length - 1]
        // diatanceä»£è¡¨çå°±æ¯å¨è¯¥è½¨éä¸å¼¹å¹lastItemå·²ç»è¡èµ°çè·ç¦»
        let distance = (lastItem.rollSpeed * (Date.now() - lastItem.startTime)) / 1000
        if (
          distance > lastItem.width &&
          (data.rollSpeed <= lastItem.rollSpeed ||
            (distance - lastItem.width) / (data.rollSpeed - lastItem.rollSpeed) >
              (this.container.clientWidth + lastItem.width - distance) / lastItem.rollSpeed)
        ) {
          y.push(i)
        } else {
          y = []
        }
      }
      if (y.length >= data.useTracks) {
        data.y = y
        data.y.forEach((id) => {
          this.tracks[id].datas.push(data)
        })
        break
      }
    }
  }

  removeDataFromTrack(data: DanmakuData) {
    data.y.forEach((id) => {
      let datas = this.tracks[id].datas
      let index = datas.indexOf(data)
      if (index === -1) return
      datas.splice(index, 1)
    })
  }

  startAnimate(data: DanmakuData) {
    // å¦æå½åæ¯æåçè¯åè¯¥å¼¹å¹ä¸åºè¯¥å¼å¯å¨ç»
    if (this.isPaused || this.player.video.paused) {
      this.queue.add(data)
      this.removeDataFromTrack(data)
      return
    }
    if (this.isHidden) {
      data.dom.style.visibility = 'hidden'
    }
    // moovingQueueä¸­å­å¨çé½æ¯å¨è¿å¨ä¸­çå¼¹å¹
    this.moovingQueue.push(data)
    data.dom.style.transform = `translateX(-${data.totalDistance}px)`
    data.dom.style.transition = `transform ${data.rollTime}s linear`
    data.dom.ontransitionend = (e) => {
      this.container.removeChild(data.dom)
      this.removeDataFromTrack(data)
      let index = this.moovingQueue.indexOf(data)
      if (index > -1) {
        this.moovingQueue.splice(index, 1)
      }
    }
  }

  // æ¸ç©ºææçå¼¹å¹ï¼åæ¬æ­£å¨è¿å¨ä¸­çæèè¿å¨ç¼å²åºæªè¢«éæ¾ç
  flush() {
    console.log('flush')
    window.clearTimeout(this.timer)
    this.timer = null

    this.moovingQueue.forEach((data) => {
      data.dom.parentNode?.removeChild(data.dom)
      data.dom.ontransitionstart = null
      data.dom.ontransitionend = null
    })
    this.queue.forEach((data) => {
      if ([...this.container.children].includes(data.dom)) {
        data.dom.parentNode?.removeChild(data.dom)
        data.dom.ontransitionstart = null
        data.dom.ontransitionend = null
      }
    })
    // æ¸ç©ºè½¨éä¸çæææ°æ®
    this.tracks.forEach((obj) => {
      obj.datas = []
    })
    this.moovingQueue = []
    this.queue.clear()
  }

  // éèææçå¼¹å¹
  close() {
    this.isHidden = true
    this.moovingQueue.forEach((data) => {
      data.dom.style.visibility = 'hidden'
    })

    this.queue.forEach((data, index) => {
      if (data.dom) {
        data.dom.style.visibility = 'hidden'
      }
    })
  }

  open() {
    this.isHidden = false
    this.moovingQueue.forEach((data) => {
      data.dom.style.visibility = ''
    })

    this.queue.forEach((data, index) => {
      if (data.dom) {
        data.dom.style.visibility = ''
      }
    })
  }

  //* è®¾ç½®å¼¹å¹çéæåº¦
  setOpacity(opacity: number) {
    this.opacity = opacity
    this.moovingQueue.forEach((data) => {
      data.dom.style.opacity = opacity + ''
    })
  }

  //* è®¾ç½®å¼¹å¹è½¨éæ¯æ°ç®
  setTrackNumber(num?: number) {
    if (!num) {
      this.trackNumber = (this.container.clientHeight / this.trackHeight) * this.showScale
      return
    }
    this.showScale = num
    this.trackNumber = (this.container.clientHeight / this.trackHeight) * this.showScale
  }

  setFontSize(scale: number) {
    this.fontSizeScale = scale
    this.moovingQueue.forEach((data) => {
      data.dom.style.fontSize = data.fontSize * this.fontSizeScale + 'px'
    })
  }

  setPaused(val: boolean) {
    this.isPaused = val
  }

  // // ä¸¢å¼ä¸é¨åæ²¡ç¨æèè¿æ¶çå¼¹å¹
  // disCard(start: number, end: number) {
  //   this.queue.splice(start, end - start + 1)
  // }

  // clearOutdatedDanmaku(currentTime: number, interval: number) {
  //   this.queue = this.queue.filter((item) => {
  //     if (currentTime - item.timestamp > interval) {
  //       return false
  //     }
  //     return true
  //   })
  // }
}
